  --ff-primary: "PT Sans", Calibri, Tahoma, sans-serif ;
}

body {
  font-family: var(--ff-primary);
  /*font-weight: 600;*/
  line-height: 1.6;
  height: 100%;
  width: 100%;
}

body, h1, h2, h3, p {
  margin: 0;
}

h2 {
  margin-block-end: 1rem;
}

p {
  margin-block-start: 0;
}

section {
  padding: 1rem 0;
}

img {
  display: block;
  max-width: 100%;
}

input {
  margin-left: auto;
  margin-right: auto;
  font-size: 16px;
  width: 90%;
  height: 40px;
  border-width: 3px;
  border-color: #775139;
  background-color: #FFFFFF;
  border-style: solid;
  border-radius: 5px;
  box-shadow: 0px 0px 5px rgba(66, 66, 66, 0.75);
  text-shadow: 0px 0px 5px rgba(66, 66, 66, 0.75);
}

input:hover {
  background-color: antiquewhite;
}

input:active {
  background-color: azure;
}

header {
  display: flex;
  padding-block: 1rem;
}

.container {
  margin-inline: auto;
  padding: 1rem;
  width: min(90%, 70.5rem);
  height: auto;
}

.navigation {
  display: flex;
  justify-content: space-evenly;
  margin-inline: auto;
  max-width: 500px;
}

.knowledgeSection {
  height: 100%;
}

.bg-creme {
  background-color: cornsilk;
}

.bg-dark {
  background-color: #333029;
}

.bg-light {
  background-color: antiquewhite;
}

.authorizationBlock {
  padding-block: 1rem;
}

.authorizationPanel {
  max-width: 30rem;
  height: 200px;
  margin: auto;
  padding: 1rem;
  /*padding-left: 30px;*/
  /*padding-right: 30px;*/
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 5px;
  background-color: burlywood;
  border: 5px solid #000000;
  border-radius: 25px;
}

.invalid {
  border-color: red;
}

.knowledgePanel {
  /*border: solid 5px #333029;*/
  margin-inline: 5rem;
  display: grid;
  /*grid-template-columns: repeat(auto-fill, minmax(10em, 1fr));*/
  grid-gap: 3rem;
  justify-content: center;
}

.knowledgeBlock {
  display: flex;
  text-align: left;
  height: auto;
  background-color: white;
  /*max-width: 300px;*/
  flex-direction: column;
  padding: 1rem;
  border: solid burlywood 5px;
  border-radius: 20px;
}

.knowledgeBlock:hover {
  background-color: burlywood;
}

.knowledgeBlockImage {
  object-fit: cover;
  width: 100%;
  height: 8em;
}

.footer {
  position: sticky;
  bottom: 0;
}

.pagination {
  display: flex;
  justify-content: center;
  margin-inline: 15rem;
}

.pageButton, .pageButton_highlighted, .pageButtonInactive {
  background: #eae0c2;
  border-radius: 15px;
  border: 2px solid #333029;
  display: inline-block;
  margin-right: 8px;
  cursor: pointer;
  color: #505739;
  font-size: 14px;
  font-weight: bold;
  padding: 12px 16px;
  text-decoration: none;
  text-shadow: 0 1px 0 #ffffff;
}

.pageButton_highlighted {
  background: #fff8f8;
  border: none;
}

.pageButton_inactive {
  background-color: transparent;
  border-color: transparent;
  outline: none;
  cursor: default;
}

.submitButton {
  margin: auto;
}

.pageButton:hover {
  background-color: #ccc2a6;
}

.pageButton:active {
  position: relative;
  top: 1px;
  background-color: #505739;
}

.bg {
  position: sticky;
  z-index: -1;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: 300% 300%;
  background-image: linear-gradient(-45deg, #ffae00 0%, #ff9100 25%, #ffe35a 51%, #ff357f 100%);
  -webkit-animation: AnimateBG 20s ease infinite;
  animation: AnimateBG 20s ease infinite;
}

@-webkit-keyframes AnimateBG {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes AnimateBG {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
